/*  BASICS
  ============================== */
* {
  box-sizing: border-box; }

html,
body,
#root,
.App {
  height: 100%; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.container {
  max-width: 1180px;
  padding: 32px 16px;
  margin: auto; }

/*  COMMONS
  ============================== */
select,
input[type='button'] {
  font-size: 16px;
  font-weight: 700;
  color: #444;
  margin: 0 0 0 8px;
  border: 1px solid #aaa;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  border-radius: .5em;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: white; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center; }

header {
  width: 100%;
  background-color: black;
  color: white; }

select {
  padding: .6em 1.5em .5em .8em;
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-position: calc(100% - 8px) 15px;
  background-size: .65em auto, 100%; }

select::-ms-expand {
  display: none; }

select:hover {
  border-color: #888; }

select:focus {
  border-color: #aaa;
  color: #222;
  outline: none; }

select option {
  font-weight: normal; }

main {
  width: 100%;
  padding-top: 12px; }
  main .container {
    display: flex;
    flex-wrap: wrap; }

/*  COLORS
  ============================== */
.movie {
  position: relative;
  flex-basis: 100%;
  margin: 12px;
  padding: 16px;
  text-align: center;
  box-shadow: 0 0 6px 0 #aaa; }
  .movie h4 {
    margin: 0;
    text-align: left;
    font-weight: 400;
    font-style: italic; }

.delete {
  position: absolute;
  top: 4px;
  right: 4px;
  background: 0;
  border: 0;
  padding: 8px;
  cursor: pointer;
  outline: none;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: -webkit-transform .8s;
  transition: -webkit-transform .8s;
  transition: transform .8s;
  transition: transform .8s, -webkit-transform .8s; }
  .delete:hover {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg); }

.close {
  width: 20px; }

/*  GAUGE
  ============================== */
.gauge-container {
  position: relative;
  overflow: hidden; }
  .gauge-container .indicator {
    position: absolute;
    width: 18px;
    left: 50%;
    -webkit-transition: -webkit-transform 1.5s cubic-bezier(0.3, -0.4, 0.6, 1.5);
    transition: -webkit-transform 1.5s cubic-bezier(0.3, -0.4, 0.6, 1.5);
    transition: transform 1.5s cubic-bezier(0.3, -0.4, 0.6, 1.5);
    transition: transform 1.5s cubic-bezier(0.3, -0.4, 0.6, 1.5), -webkit-transform 1.5s cubic-bezier(0.3, -0.4, 0.6, 1.5); }
  .gauge-container .gauge {
    width: 180px; }

.gauge-inactive .indicator {
  -webkit-transform: translate(-50%, 43px) rotate(0deg) !important;
          transform: translate(-50%, 43px) rotate(0deg) !important;
  -webkit-transition: -webkit-transform 0s;
  transition: -webkit-transform 0s;
  transition: transform 0s;
  transition: transform 0s, -webkit-transform 0s; }

/*  EVALUATION USER
  ============================== */
.evaluation-buttons,
.evaluation-count {
  display: flex;
  justify-content: space-around;
  margin: 12px 0; }

.dislike,
.like {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75px;
  width: 75px;
  border-radius: 50%;
  -webkit-transition: background-color .25s ease;
  transition: background-color .25s ease;
  cursor: pointer; }

.dislike {
  padding-top: 8px;
  background-color: #e73127; }
  .dislike:hover,
  .dislike :focus,
  .dislike :active {
    background-color: #ff473d; }

.like {
  padding-bottom: 8px;
  background-color: #0d730d; }
  .like:hover,
  .like :focus,
  .like :active {
    background-color: #009900; }

.thumb {
  width: 36px; }

.evaluation-count p {
  min-width: 75px;
  margin: 0;
  font-size: 20px;
  font-weight: 600; }

.evaluation-user {
  height: 21px;
  margin-top: 8px; }

.evaluation-like {
  background-color: rgba(13, 115, 13, 0.3); }

.evaluation-dislike {
  background-color: rgba(231, 49, 39, 0.3); }

/*  CARDS SIZE
  ============================== */
@media screen and (max-width: 479px) {
  .movie {
    margin: 12px 0; } }

@media screen and (min-width: 480px) and (max-width: 767px) {
  .movie {
    max-width: calc((100% - 24px ) / 2); }
    .movie:nth-of-type(2n + 1) {
      margin-left: 0; }
    .movie:nth-of-type(2n) {
      margin-right: 0; } }

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .movie {
    max-width: calc((100% - 48px) / 3); }
    .movie:nth-of-type(3n + 1) {
      margin-left: 0; }
    .movie:nth-of-type(3n) {
      margin-right: 0; } }

@media screen and (min-width: 1024px) {
  .movie {
    max-width: calc((100% - 72px) / 4); }
    .movie:nth-of-type(4n + 1) {
      margin-left: 0; }
    .movie:nth-of-type(4n) {
      margin-right: 0; } }

footer {
  flex-grow: 1;
  width: 100%;
  background-color: lightgrey; }
  footer .container {
    display: flex;
    justify-content: space-around; }

@media screen and (max-width: 599px) {
  footer .container {
    flex-direction: column-reverse;
    align-items: center; }
    footer .container > div:last-of-type {
      margin-bottom: 8px; } }

@media screen and (max-width: 479px) {
  .pagination {
    width: 100%; } }

input[type='button'] {
  display: inline-block;
  padding: .6em .8em .5em .8em;
  text-align: center;
  cursor: pointer; }
  input[type='button']:hover,
  input[type='button'] :focus,
  input[type='button'] :active {
    border-color: #888; }
  input[type='button']:disabled {
    background-color: #c7c7c7;
    cursor: not-allowed; }

@media screen and (max-width: 479px) {
  input[type='button'] {
    display: block;
    width: 100%;
    margin: 8px 0; } }

